import React, { PropTypes } from 'react';
import ReactDOM from 'react-dom';
import KeyCode from 'rc-util/lib/KeyCode';
import Animate from 'rc-animate';
import LazyRenderBox from './LazyRenderBox';
import isString from 'lodash/isString';
import Draggable from '../../ecology9/wea-draggable';
import util from '../../_util/util';

let uuid = 0;
let openCount = 0;

// Measure scrollbar width for padding body during modal show/hide
const scrollbarMeasure = {
  position: 'absolute',
  top: '-9999px',
  width: '50px',
  height: '50px',
  overflow: 'scroll',
};

/* eslint react/no-is-mounted:0 */

function noop() {
}

function getScroll(w, top) {
  let ret = w[`page${top ? 'Y' : 'X'}Offset`];
  const method = `scroll${top ? 'Top' : 'Left'}`;
  if (typeof ret !== 'number') {
    const d = w.document;
    ret = d.documentElement[method];
    if (typeof ret !== 'number') {
      ret = d.body[method];
    }
  }
  return ret;
}

function setTransformOrigin(node, value) {
  const style = node.style;
  ['Webkit', 'Moz', 'Ms', 'ms'].forEach((prefix) => {
    style[`${prefix}TransformOrigin`] = value;
  });
  style[`transformOrigin`] = value;
}

function offset(el) {
  const rect = el.getBoundingClientRect();
  const pos = {
    left: rect.left,
    top: rect.top,
  };
  const doc = el.ownerDocument;
  const w = doc.defaultView || doc.parentWindow;
  pos.left += getScroll(w);
  pos.top += getScroll(w, 1);
  return pos;
}

const Dialog = React.createClass({
  propTypes: {
    onAfterClose: PropTypes.func,
    onClose: PropTypes.func,
    closable: PropTypes.bool,
    maskClosable: PropTypes.bool,
    visible: PropTypes.bool,
    mousePosition: PropTypes.object,
    wrapStyle: PropTypes.object,
    prefixCls: PropTypes.string,
    wrapClassName: PropTypes.string,
    onSpackClick: PropTypes.func,
    onOk: PropTypes.func,
  },

  getDefaultProps() {
    return {
      onAfterClose: noop,
      onClose: noop,
    };
  },

  getInitialState() {
    return {
      // drgX: 0,
      // drgY: 0,
      draggableKey: `draggable-${new Date().getTime()}`,
    };
  },

  componentWillMount() {
    this.titleId = `rcDialogTitle${uuid++}`;
  },

  componentDidMount() {
    this.componentDidUpdate({});
  },
  componentWillReceiveProps(nextProps,nextState) {
    if ('visible' in nextProps && this.props.visible !== nextProps.visible) {
      if(!nextProps.visible) {
        // this.setState({drgX: 0, drgY: 0});
        this.removeScrollingEffect();
      } else {
        this.addScrollingEffect();
      }
    }
  },
  componentDidUpdate(prevProps) {
    const props = this.props;
    const mousePosition = this.props.mousePosition;
    const dialogNode = ReactDOM.findDOMNode(this.refs.dialog);
    if (props.visible) {
      // first show
      if (!prevProps.visible) {
        // this.refs.draggable.setState({x: 0, y: 0});
        this.lastOutSideFocusNode = document.activeElement;
        this.addScrollingEffect();
        this.refs.wrap.focus();
        // if (mousePosition) {
        //   const elOffset = offset(dialogNode);
        //   setTransformOrigin(dialogNode,
        //     `${mousePosition.x - elOffset.left}px ${mousePosition.y - elOffset.top}px`);
        // } else {
        //   setTransformOrigin(dialogNode, '');
        // }
      }
    } else if (prevProps.visible) {
      dialogNode.style['transform'] = 'none';
      this.setState({draggableKey: `draggable-${new Date().getTime()}`})
      // this.refs.draggable.setState({x: 0, y: 0});
      if (props.mask && this.lastOutSideFocusNode) {
        try {
          this.lastOutSideFocusNode.focus();
        } catch (e) {
          this.lastOutSideFocusNode = null;
        }
        this.lastOutSideFocusNode = null;
      }
    }
  },

  onAnimateLeave() {
    // need demo?
    // https://github.com/react-component/dialog/pull/28
    if (this.refs.wrap) {
      this.refs.wrap.style.display = 'none';
    }
    this.removeScrollingEffect();
    this.props.onAfterClose();
  },

  onMaskClick(e) {
    if (e.target === e.currentTarget && this.props.closable && this.props.maskClosable) {
      this.close(e);
    }
  },

  onKeyDown(e) {
    const props = this.props;
    if (props.closable && props.keyboard) {
      if (e.keyCode === KeyCode.ESC) {
        this.close(e);
      }
    }
    // keep focus inside dialog
    if (props.visible) {
      if (e.keyCode === KeyCode.TAB) {
        const activeElement = document.activeElement;
        const dialogRoot = this.refs.wrap;
        const sentinel = this.refs.sentinel;
        if (e.shiftKey) {
          if (activeElement === dialogRoot) {
            sentinel.focus();
          }
        } else if (activeElement === this.refs.sentinel) {
          dialogRoot.focus();
        }
      }

      if (e.keyCode === KeyCode.SPACE) {
        props.onSpackClick && props.onSpackClick(e);
      }

      if (e.KeyCode === KeyCode.ENTER) {
        props.onOk && props.onOk(e);
      }
    }
  },
  onDragStop(e, position) {
    let {x, y} = position;
    let limit = this.dialogLimit(x, y);
    x = limit.x; y = limit.y;
    // this.setState({drgX: x, drgY: y});
    setTimeout(()=>{
      $(this.refs.dialog).css('transform', `translate(${x}px, ${y}px)`)
    }, 10)
  },
  dialogLimit(x, y) {
    let boundLeft, boundTop, boundRight, boundBottom;
    const dom = $(this.refs.wrap).find('.ant-modal-content');
    const bodyHeight = $(document.body).height();
    const bodyWidth = $(document.body).width();
    const domHeight = dom.outerHeight();
    const domWidth = dom.outerWidth();
    boundLeft = bodyWidth/2 + domWidth/2 - 100;
    boundTop = bodyHeight/2 - domHeight/2 - 20;
    boundRight = bodyWidth/2 + domWidth/2 - 100;
    boundBottom = bodyHeight/2 + domHeight/2 - 50;
    if (x > 0) {
      if (x > boundRight) x = boundRight;
    } else {
      if (x < -boundLeft) x = -boundLeft;
    }
    if (y > 0) {
      if (y > boundBottom) y = boundBottom;
    } else {
      if (y < -boundTop) y = -boundTop;
    }
    return {x, y};
  },
  getDialogElement() {
    const {draggableKey} = this.state;
    const props = this.props;
    const {draggable = true} = props;
    const closable = props.closable;
    const prefixCls = props.prefixCls;
    const dest = {};
    if (props.width !== undefined) {
      dest.width = props.width;
    }
    if (props.height !== undefined) {
      dest.height = props.height;
    }


    let footer;
    if (props.footer) {
      footer = (<div className={`${prefixCls}-footer`} ref="footer">
        {
          isString(props.footer)?
            <div  dangerouslySetInnerHTML={{__html: props.footer}}></div>
            :props.footer
        }
      </div>);
    }
    const nodrag = util.isBelowIE9() || !draggable; // IE9 暂时不让拖拽

    let header;
    if (props.title) {
      header = (<div className={`${prefixCls}-header`} ref="header">
        <div className={`${prefixCls}-title ${nodrag?'':'cursor-move'}`} id={this.titleId}>
          {props.title}
        </div>
      </div>);
    }

    let closer;
    if (closable) {
      closer = (<span
        onClick={this.close}
        className={`${prefixCls}-close`}
      >
        <span className={`${prefixCls}-close-x`}/>
      </span>);
    }

    const style = {
      ...props.style,
      ...dest,
    };
    const transitionName = this.getTransitionName();
    const dialogElement =
          nodrag?
          (<LazyRenderBox
              role="document"
              ref="dialog"
              style={style}
              className={`${prefixCls} ${props.className || ''}`}
              visible={props.visible}
              resize={props.resize}
            >
              <div className={`${prefixCls}-content`}>
                {closer}
                {header}
                <div className={`${prefixCls}-body`} style={props.bodyStyle} ref="body">
                  {props.children}
                </div>
                {footer}
              </div>
              <div tabIndex="0" ref="sentinel" style={{ width: 0, height: 0, overflow: 'hidden' }}>
                sentinel
              </div>
          </LazyRenderBox>)
          :
          (<Draggable
              handle=".ant-modal-title"
              ref='draggable'
              key={draggableKey}
              // position={{x: drgX, y: drgY}}
              onStop={this.onDragStop.bind(this)}
          >
            <LazyRenderBox
              role="document"
              ref="dialog"
              style={style}
              className={`${prefixCls} ${props.className || ''}`}
              visible={props.visible}
              resize={props.resize}
            >
              <div className={`${prefixCls}-content`}>
                {closer}
                {header}
                <div className={`${prefixCls}-body`} style={props.bodyStyle} ref="body">
                  {props.children}
                </div>
                {footer}
              </div>
              <div tabIndex="0" ref="sentinel" style={{ width: 0, height: 0, overflow: 'hidden' }}>
                sentinel
              </div>
            </LazyRenderBox>
          </Draggable>)
    return (
        !props.disableAnimate ? <Animate
        key="dialog"
        showProp="visible"
        onLeave={this.onAnimateLeave}
        transitionName={transitionName}
        component=""
        transitionAppear
      >
        {dialogElement}
      </Animate> : dialogElement
    );
  },

  getZIndexStyle() {
    const style = {};
    const props = this.props;
    if (props.zIndex !== undefined) {
      style.zIndex = props.zIndex;
    }
    return style;
  },

  getWrapStyle() {
    return {
      ...this.getZIndexStyle(),
      ...this.props.wrapStyle,
    };
  },

  getMaskElement() {
    const props = this.props;
    let maskElement;
    if (props.mask) {
      const maskTransition = this.getMaskTransitionName();
      maskElement = (
        <LazyRenderBox
          style={this.getZIndexStyle()}
          key="mask"
          className={`${props.prefixCls}-mask`}
          hiddenClassName={`${props.prefixCls}-mask-hidden`}
          visible={props.visible}
        />
      );
      if (maskTransition) {
        maskElement = (
          <Animate
            key="mask"
            showProp="visible"
            transitionAppear
            component=""
            transitionName={maskTransition}
          >
            {maskElement}
          </Animate>
        );
      }
    }
    return maskElement;
  },

  getMaskTransitionName() {
    const props = this.props;
    let transitionName = props.maskTransitionName;
    const animation = props.maskAnimation;
    if (!transitionName && animation) {
      transitionName = `${props.prefixCls}-${animation}`;
    }
    return transitionName;
  },

  getTransitionName() {
    const props = this.props;
    let transitionName = props.transitionName;
    const animation = props.animation;
    if (!transitionName && animation) {
      transitionName = `${props.prefixCls}-${animation}`;
    }
    return transitionName;
  },

  getElement(part) {
    return this.refs[part];
  },

  setScrollbar() {
    if (this.bodyIsOverflowing && this.scrollbarWidth) {
      document.body.style.paddingRight = `${this.scrollbarWidth}px`;
    }
  },

  addScrollingEffect() {
    document.body.style.overflow = 'hidden';
    openCount++;
    if (openCount !== 1) {
      return;
    }
    this.checkScrollbar();
    this.setScrollbar();
    // this.adjustDialog();
  },

  removeScrollingEffect() {
    document.body.style.overflow = '';
    openCount--;
    if (openCount !== 0) {
      return;
    }
    this.resetScrollbar();
    // this.resetAdjustments();
  },

  close(e) {
    // reset modal drag position
    // this.setState({drgX:0, drgY:0});
    // $(this.refs.dialog).css('transform', 'none');
    this.removeScrollingEffect();
    this.props.onClose(e);
  },

  checkScrollbar() {
    let fullWindowWidth = window.innerWidth;
    if (!fullWindowWidth) { // workaround for missing window.innerWidth in IE8
      const documentElementRect = document.documentElement.getBoundingClientRect();
      fullWindowWidth = documentElementRect.right - Math.abs(documentElementRect.left);
    }
    this.bodyIsOverflowing = document.body.clientWidth < fullWindowWidth;
    if (this.bodyIsOverflowing) {
      this.scrollbarWidth = this.measureScrollbar();
    }
  },
  resetScrollbar() {
    document.body.style.paddingRight = '';
  },

  measureScrollbar() {
    if (this.scrollbarWidth !== undefined) {
      return this.scrollbarWidth;
    }
    const scrollDiv = document.createElement('div');
    for (const scrollProp in scrollbarMeasure) {
      if (scrollbarMeasure.hasOwnProperty(scrollProp)) {
        scrollDiv.style[scrollProp] = scrollbarMeasure[scrollProp];
      }
    }
    document.body.appendChild(scrollDiv);
    const scrollbarWidth = scrollDiv.offsetWidth - scrollDiv.clientWidth;
    document.body.removeChild(scrollDiv);
    this.scrollbarWidth = scrollbarWidth;
    return scrollbarWidth;
  },

  adjustDialog() {
    if (this.refs.wrap && this.scrollbarWidth) {
      const modalIsOverflowing =
        this.refs.wrap.scrollHeight > document.documentElement.clientHeight;
      this.refs.wrap.style.paddingLeft =
        `${!this.bodyIsOverflowing && modalIsOverflowing ? this.scrollbarWidth : ''}px`;
      this.refs.wrap.style.paddingRight =
        `${this.bodyIsOverflowing && !modalIsOverflowing ? this.scrollbarWidth : ''}px`;
    }
  },

  resetAdjustments() {
    if (this.refs.wrap) {
      this.refs.wrap.style.paddingLeft = this.refs.wrap.style.paddingLeft = '';
    }
  },
  render() {
    const props = this.props;
    const prefixCls = props.prefixCls;
    const style = this.getWrapStyle();
    // clear hide display
    // and only set display after async anim, not here for hide
    if (props.visible) {
      style.display = null;
    } else {
      style.display = 'none';
    }
    return (<div className={`${props.parentClassName}`}>
      {this.getMaskElement()}
        <div
          tabIndex="-1"
          onKeyDown={this.onKeyDown}
          className={`${prefixCls}-wrap ${props.wrapClassName || ''}`}
          ref="wrap"
          onClick={this.onMaskClick}
          role="dialog"
          aria-labelledby={props.title ? this.titleId : null}
          style={style}
        >
          {this.getDialogElement()}

        </div>
    </div>);
  },
});

export default Dialog;



